.tel-block{
	width: 100%;
	margin: 0 0 25px 70px;
	position: relative;
	padding: 25px 70px;
	text-align: center;
	max-width: 360px;
	background: #fff;
	border: 1px solid #fc7a3c;
  border-radius: 10px;
  box-shadow: 2.5px 4.33px 5px 0px rgba(0, 0, 0, 0.2);
  @nest &:before{
  	position: absolute;
  	content: '';
  	left: -60px;
  	bottom: 50px;
  	z-index: -1;
  	background: resolve('tel1.jpg');
  	width: width('tel1.jpg');
  	height: height('tel1.jpg');
  }
  @nest &:after{
  	position: absolute;
  	content: '';
  	top: 10px;
  	right: -110px;
  	z-index: -1;
  	background: resolve('tel2.jpg');
  	width: width('tel2.jpg');
  	height: height('tel2.jpg');
  }
  @nest &--indent{
    padding: 25px 59px;
    @nest &:before{
      left: -110px;
      bottom: 25px;
      background: resolve('tel3.jpg');
      width: width('tel3.jpg');
      height: height('tel3.jpg');
    }
  }
  @media (max-width: 991px){
  	margin: 25px auto;
  }
  @media (max-width: 512px){
  	padding: 25px;
  	@nest &:before{
  		display: none;
  	}
  	@nest &:after{
  		display: none;
  	}
  }
  @nest &__title{
  	font-size: 18px;
  	color: #302f2f;
  	font-weight: 700;
  	line-height: 24px;
  	display: block;
  	margin: 0 0 30px;
  }
  @nest &__text{
  	display: block;
  	font-size: 16px;
  	color: #302f2f;
  	line-height: 24px;
  	margin: 0 0 30px;
  }
  @nest &__bot{
  	font-size: 16px;
  	color: #302f2f;
  	font-weight: 500;
  	display: block;
  	line-height: 24px;
  }
}