.instr-block{
	max-width: 750px;
	margin: auto;
	min-height: 410px;
	margin-bottom: 45px;
	background: resolve('instr.jpg') no-repeat center;
	@media (max-width: 767px){
		display: none;
	}
	@nest &__row{
		clear: fix;
	}
	@nest &__item{
		float: left;
		width: 165px;
		height: 120px;
		background: #fff;
		text-align: center;
		margin-bottom: 50px;
		border-radius: 5px;
		position: relative;
		border: 1px solid #fc7a3c;
		@nest &--right{
			float: right;
		}
		@nest &--first{
			@nest &:after{
				position: absolute;
				content: '';
				left: 100%;
				top: 0;
				background: resolve('line1.png');
				width: width('line1.png');
				height: height('line1.png');
			}
		}
		@nest &--second{
			@nest &:after{
				position: absolute;
				content: '';
				right: 97%;
				top: 0;
				background: resolve('line2.png');
				width: width('line2.png');
				height: height('line2.png');
			}
		}
		@nest &--third{
			@nest &:after{
				position: absolute;
				content: '';
				left: 100%;
				bottom: 0;
				background: resolve('line3.png');
				width: width('line3.png');
				height: height('line3.png');
			}
		}
		@nest &--fourth{
			@nest &:after{
				position: absolute;
				content: '';
				right: 97%;
				bottom: 0;
				background: resolve('line4.png');
				width: width('line4.png');
				height: height('line4.png');
				@media (max-width: 991px){
					right: 100%;
					bottom: -20px;
					width: 111%;
				}
			}
		}
		@nest &-text{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			padding: 0 10px;
			top: 50%;
			display: block;
			font-size: 12px;
			color: #302f2f;
			font-weight: 500;
			line-height: 24px;
			transform: translateY(-50%);
		}
	}
}