.price-table{
	width: 100%;
	max-width: 555px;
	@nest &--size{
		table-layout: fixed;
		max-width: 100%;
	}
	@nest &__tr{
		@nest &--bg{
			background: #fff4ef;
		}
	}
	@nest &__td{
		vertical-align: top;
		@nest &--lg{
			width: 70%;
		}
		@nest &--md{
			width: 50%;
		}
		@nest &--sm{
			width: 30%;
		}
		@nest &--indent-left{
			padding-left: 20px;
		}
	}
	@nest &__title{
		font-size: 18px;
		color: #ff4900;
		font-weight: 700;
		display: block;
		margin: 15px 0;
		font-family: var(--font);
		@nest &--table{
			@media (max-width: 512px){
				font-size: 14px;
			}
		}
	}
	@nest &__text{
		font-size: 14px;
		font-weight: 300;
		color: #302f2f;
		display: block;
		padding: 2px 0 6px;
		@nest &--color{
			color: #5054d3;
		}
		@media (max-width: 512px){
			font-size: 13px;
		}
	}
}