.news-block{
	font-size: 0;
	margin: 50px 0;
	@nest &__image{
		width: 15%;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 991px){
			width: auto;
			vertical-align: middle;
		}
	}
	@nest &__title{
		width: 20%;
		font-size: 18px;
		font-weight: 700;
		padding-left: 30px;
		font-family: var(--font);
		color: #ff4900;
		line-height: 24px;
		letter-spacing: .45px;
		display: inline-block;
		vertical-align: top;
		margin-top: 50px;
		@media (max-width: 1199px){
			margin-top: 30px;
		}
		@media (max-width: 991px){
			width: 30%;
			vertical-align: middle;
			margin-top: 0;
		}
		@media (max-width: 512px){
			width: 100%;
			padding-left: 0;
			margin-top: 15px;
		}
	}
	@nest &__text{
		width: 65%;
		display: inline-block;
		vertical-align: top;
		margin-top: 50px;
		@media (max-width: 1199px){
			margin-top: 30px;
		}
		@media (max-width: 991px){
			width: 100%;
		}
	}
}