.tabs-list{
	padding: 0;
	margin: 0;
	list-style: none;
	margin-bottom: 40px;
	@nest &__item{
		display: inline-block;
		margin-right: 140px;
		@media (max-width: 1199px){
			margin-right: 35px;
		}
		@media (max-width: 512px){
			margin-right: 15px;
		}
		@nest &:last-child{
			margin-right: 0;
		}
		@nest & button{
			font-size: 18px;
			color: #e5c8ba;
			font-weight: 700;
			border: 0;
			outline: none;
			padding: 0;
			line-height: 24px;
			letter-spacing: .45px;
			font-family: var(--font);
			background-color: transparent;
			transition: color 300ms;
			@nest &:hover{
				color: #fc7a3c;
			}
			@media (max-width: 512px){
				font-size: 15px;
			}
		}
		@nest &_active{
			@nest & button{
				color: #fc7a3c;
				border-bottom: 2px solid #fec5a9;
			}
		}
	}
}