.order-tovar{
	max-width: 165px;
	margin: auto;
	@media (max-width: 1199px){
		margin-bottom: 32px;
	}
	@nest &__image{
		min-height: 170px;
		background: #fff4ef;
		position: relative;
		transition: box-shadow 300ms;
		@nest &-in{
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
		@nest &--hover{
			box-shadow: 0px 2px 7.2px 0.8px rgba(6, 6, 6, 0.15);
		}
	}
	@nest &__title{
		font-size: 14px;
		color: #000;
		font-weight: 500;
		display: block;
		text-align: center;
		max-width: 120px;
		margin: 8px auto;
		letter-spacing: .14px;
	}
	@nest &__bot{
		clear: fix;
	}
	@nest &__price{
		font-size: 18px;
		font-weight: 500;
		color: var(--blue);
		float: left;
	}
	@nest &__btn{
		float: right;
		color: #fff;
		min-height: 24px;
		border: 0;
		outline: none;
		padding: 0;
		width: 100px;
		font-size: 14px;
		font-weight: 700;
		border-radius: 4px;
		background: #fc7b3d;
		font-family: var(--font);
		text-shadow: 0px 1px 0px rgba(54, 54, 54, 0.2);
		transition: background 300ms, box-shadow 300ms;
		@nest &:hover{
			background: #ff5300;
			box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
		}
	}
}