.main-slider{
	@nest &__item{
		outline: none;
		padding: 40px 0 50px 95px;
		position: relative;
		@media (max-width: 767px){
			padding-left: 30px;
		}
		@nest &:before{
			position: absolute;
			content: '';
			right: 0;
			bottom: -1px;
			background: resolve('slide1.jpg');
			width: width('slide1.jpg');
			height: height('slide1.jpg');
			@media (max-width: 1199px){
				display: none;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 115px;
			bottom: 0;
			background: resolve('slide2.jpg');
			width: width('slide2.jpg');
			height: height('slide2.jpg');
			@media (max-width: 767px){
				left: auto;
				right: 0;
			}
			@media (max-width: 512px){
				display: none;
			}
		}
	}
	@nest &__title{
		font-size: 60px;
		font-weight: 700;
		color: var(--blue);
		line-height: 60px;
		max-width: 565px;
		margin-bottom: 25px;
		position: relative;
		z-index: 10;
		@nest &-in{
			color: var(--orange);
		}
		@media (max-width: 767px){
			font-size: 35px;
			line-height: normal;
		}
		@media (max-width: 512px){
			font-size: 30px;
		}
	}
	@nest &__block{
		max-width: 285px;
		margin-left: 260px;
		position: relative;
		z-index: 10;
		@nest &-title{
			display: block;
			font-size: 30px;
			font-weight: 700;
			color: var(--blue);
			line-height: 30px;
			margin-bottom: 15px;
			@media (max-width: 767px){
				font-size: 25px;
				line-height: normal;
				margin-bottom: 0;
			}
			@media (max-width: 512px){
				font-size: 20px;
			}
		}
		@media (max-width: 767px){
			margin-left: 0;
		}
	}
	@nest & .slick-dots{
		position: absolute;
		content: '';
		left: 30px;
		top: 50%;
		padding: 0;
		margin: 0;
		list-style: none;
		transform: translateY(-50%);
		@media (max-width: 767px){
			left: 0;
		}
		@nest & li{
			display: block;
			margin-bottom: 15px;
			@nest & button{
				outline: none;
				font-size: 0;
				width: 12px;
				padding: 0;
				height: 12px;
				border-radius: 50%;
				border: 1px solid #696bb7;
				background: transparent;
			}
			@nest &.slick-active{
				@nest & button{
					border: 0;
					background: #fc5200;
					box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
				}
			}
		}
	}
}