.video-block{
	position: relative;
	min-height: 335px;
	display: block;
	max-width: 455px;
	margin: auto;
	@media (max-width: 991px){
		margin-top: 20px;
	}
	@nest &:before{
		position: absolute;
		content: '';
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		transition: opacity 300ms;
		background: color(#000 alpha(50%));
	}
	@nest &:after{
		position: absolute;
		content: '';
		left: 50%;
		top: 50%;
		transition: transform 300ms;
		transform: translate(-50%, -50%);
		background: resolve('play.svg');
		width: width('play.svg');
		height: height('play.svg');
	}
	@nest &:hover{
		@nest &:before{
			opacity: 1;
		}
		@nest &:after{
			transform: scale(1.1) translate(-50%, -50%);
		}
	}
}