.tabs-slider{
	padding: 0 80px;
	@media (max-width: 1199px){
		padding: 0;
	}
	@nest &__item{
		outline: none;
	}
	@nest &__link{
		outline: none;
		display: block;
		padding: 0 15px;
		text-decoration: none;
		@media (max-width: 1199px){
			padding: 0;
		}
		@nest &:hover{
			@nest & .tabs-slider__text{
				box-shadow: 0px 2px 7.2px 0.8px rgba(6, 6, 6, 0.15);
				@nest &:after{
					opacity: 1;
				}
			}
			@nest & .tabs-slider__desc{
				opacity: 1;
			}
			@nest & .tabs-slider__name{
				color: #fc7a3c;
			}
		}
	}
	@nest &__text{
		display: block;
		margin: auto;
		width: 265px;
		height: 265px;
		position: relative;
		transition: box-shadow 300ms;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			opacity: 0;
			transition: opacity 300ms;
			background: color(#f9e4d9 alpha(90%));
		}
	}
	@nest &__desc{
		position: absolute;
		content: '';
		top: 50%;
		left: 20px;
		font-size: 14px;
		color: #313131;
		z-index: 10;
		opacity: 0;
		padding-right: 20px;
		font-weight: 300;
		line-height: 24px;
		transition: opacity 300ms;
		font-family: var(--font);
		transform: translateY(-50%);
	}
	@nest &__name{
		font-size: 14px;
		color: #313131;
		font-weight: 700;
		display: block;
		text-align: center;
		margin: 15px 0;
		transition: color 300ms;
		font-family: var(--font);
	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		top: 105px;
		outline: none;
		border: 0;
		font-size: 0;
		z-index: 20;
	}
	@nest & .slick-prev{
		left: 0;
		background: resolve('prev2.png');
		width: width('prev2.png');
		height: height('prev2.png');
	}
	@nest & .slick-next{
		right: 0;
		background: resolve('next2.png');
		width: width('next2.png');
		height: height('next2.png');
	}
}