.header-block{
	padding-right: 15px;
	display: inline-block;
	vertical-align: top;
	padding-bottom: 10px;
	@media (max-width: 1199px){
		margin-top: 20px;
	}
	@media (max-width: 512px){
		padding-right: 0;
	}
	@nest &__title{
		display: block;
		font-size: 18px;
		color: #050887;
		text-decoration: none;
	}
	@nest &__desc{
		font-size: 12px;
		color: #676767;
		font-weight: 300;
		display: block;
		line-height: 14px;
		max-width: 205px;
		font-family: var(--font);
	}
}