.nav-list{
	padding: 0;
	margin: 0;
	font-size: 0;
	list-style: none;
	@media (max-width: 767px){
		margin: 10px 0;
	}
	@nest &__item{
		width: 14.2%;
		display: inline-block;
		vertical-align: top;
		text-align: center;
		@media (max-width: 767px){
			width: 100%;
			display: block;
		}
	}
	@nest &__link{
		padding: 15px 0;
		display: block;
		font-size: 16px;
		color: #fff;
		font-weight: 500;
		transition: all 300ms;
		text-decoration: none;
		@nest &:hover{
			font-weight: 700;
			background: #fe5e12;
			box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
		}
		@media (max-width: 991px){
			font-size: 14px;
		}
		@media (max-width: 767px){
			padding: 7px 0;
			@nest &:hover{
				box-shadow: none;
			}
		}
	}
}