.header{
	padding: 25px 0 5px;
	@media (max-width: 1199px){
		text-align: center;
	}
	@media (max-width: 767px){
		padding-bottom: 25px;
	}
	@nest &__logo{
		font-size: 60px;
		color: var(--orange);
		display: inline-block;
		vertical-align: top;
		text-decoration: none;
		line-height: 30px;
		margin-right: 150px;
		transition: opacity 300ms;
		@nest &-desc{
			font-size: 12px;
			display: block;
			color: var(--blue);
		}
		@nest &:hover{
			opacity: .7;
		}
		@media (max-width: 1199px){
			display: block;
			margin-right: 0;
		}
		@media (max-width: 767px){
			font-size: 45px;
		}
	}
	@nest &__btn{
		font-size: 18px;
		color: #fff;
		width: 100%;
		border: 0;
		font-weight: 700;
		text-align: center;
		max-width: 255px;
		display: inline-block;
		padding: 13px 0 15px;
		text-decoration: none;
		font-family: var(--font);
		border-radius: 5px;
		letter-spacing: 0.9px;
		background: var(--orange);
		transition: all 300ms;
		text-shadow: 0px 1px 0px rgba(54, 54, 54, 0.2);
		@nest &:hover{
			background: #ff5300;
		}
		@media (max-width: 1199px){
			margin-top: 20px;
		}
	}
}