.instr-step{
	max-width: 360px;
	padding: 10px 20px;
	border-radius: 5px;
	position: relative;
	background: #fff;
	text-align: left;
	border: 1px solid #fc7a3c;
	box-shadow: 2.5px 4.33px 5px 0px rgba(0, 0, 0, 0.2);
	@nest &--first{
		z-index: 20;
		margin-top: 25px;
	}
	@nest &--second{
		margin-top: -50px;
		z-index: 15;
		margin-left: auto;
	}
	@nest &--third{
		margin-top: -122px;
		z-index: 10;
		padding-right: 95px;
		@media (max-width: 1199px){
			padding: 10px 20px;
		}
	}
	@media (max-width: 1199px){
		margin: 30px auto 30px 0;
	}
	@media (max-width: 767px){
		margin-left: auto;
	}
	@nest &__title{
		font-size: 18px;
		color: #302f2f;
		font-weight: 700;
		line-height: 24px;
		margin-bottom: 5px;
		letter-spacing: .45px;
		@nest &--indent{
			padding-left: 75px;
			@media (max-width: 1199px){
				padding-left: 0;
			}
		}
	}
	@nest &__text{
		font-size: 16px;
		color: #302f2f;
		font-weight: 300;
		display: block;
		margin-bottom: 5px;
		letter-spacing: -.1px;
		@nest &-in{
			font-weight: 500;
			color: var(--orange);
		}
		@nest &--indent{
			text-indent: 75px;
			@media (max-width: 1199px){
				text-indent: 0;
			}
		}
	}
	@nest &__desc{
		position: absolute;
		content: '';
		left: 0;
		bottom: -60px;
		font-size: 14px;
		color: #fc7a3c;
		font-weight: 500;
		line-height: 24px;
		padding-left: 95px;
		@nest &--indent{
			bottom: auto;
			top: -45px;
			padding-left: 0;
		}
		@media (max-width: 1199px){
			position: static;
			padding: 0;
			display: block;
		}
	}
}