.footer-block{
	max-width: 170px;
	margin: 0 20px 0 45px;
	display: inline-block;
	vertical-align: top;
	@nest &--indent-right{
		margin-right: 60px;
		margin-left: 0;
	}
	@nest &--indent{
		margin: 0;
	}
	@media (max-width: 767px){
		margin: 20px auto 0;
		display: block;
	}
	@nest &__title{
		font-size: 18px;
		color: var(--orange);
		font-weight: 500;
		display: block;
		margin-bottom: 8px;
	}
	@nest &__text{
		font-size: 12px;
		color: #302f2f;
		line-height: 24px;
		display: block;
		@nest & b{
			font-size: 14px;
		}
	}
	@nest &__number{
		font-size: 22px;
		color: #302f2f;
		display: block;
		margin-top: 10px;
		letter-spacing: -.55px;
	}
}