.order-form{
	margin-bottom: 35px;
	@media (max-width: 991px){
		text-align: center;
	}
	@nest &__title{
		font-size: 16px;
		color: #302f2f;
		display: inline-block;
		margin-right: 45px;
		@nest &-in{
			color: var(--orange);
		}
		@media (max-width: 991px){
			display: block;
			margin-bottom: 5px;
			margin-right: 0;
		}
	}
	@nest &__delivery{
		margin: 30px 0 0;
		@nest &-title{
			font-size: 16px;
			color: #302f2f;
			display: inline-block;
			margin-right: 15px;
		}
	}
	@nest &__wrap-radio{
		display: inline-block;
		@media (max-width: 512px){
			display: block;
			margin: 10px 0;
		}
	}
	@nest &__radio{
		display: none;
		@nest &:checked{
			@nest & ~ .order-form__label{
				color: var(--orange);
				@nest &:after{
					display: block;
				}
			}
		}
	}
	@nest &__label{
		cursor: pointer;
		margin-right: 20px;
		padding-right: 30px;
		position: relative;
		display: inline-block;
		@nest &:before{
			position: absolute;
			content: '';
			right: 0;
			top: 50%;
			width: 20px;
			height: 20px;
			border-radius: 50%;
			border: 1px solid #e5e5e5;
			transform: translateY(-50%);
		}
		@nest &:after{
			position: absolute;
			content: '';
			width: 6px;
			height: 6px;
			right: 7px;
			top: 50%;
			display: none;
			background: #fc7b3d;
			border-radius: 50%;
			transform: translateY(-50%);
		}
		@media (max-width: 512px){
			margin-right: 0;
		}
	}
	@nest &__inner{
		margin: 30px 0 0;
		font-size: 0;
		clear: fix;
		max-width: 750px;
		@nest &-left{
			width: 100%;
			max-width: 360px;
			float: left;
			@media (max-width: 991px){
				float: none;
				margin: auto;
			}
		}
		@nest &-right{
			width: 100%;
			max-width: 360px;
			float: right;
			@media (max-width: 991px){
				float: none;
				margin: auto;
			}
		}
	}
	@nest &__input{
		width: 100%;
		font-size: 14px;
		color: #9d9d9d;
		font-weight: 300;
		outline: none;
		border: 0;
		padding-left: 18px;
		min-height: 48px;
		margin-bottom: 24px;
		box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
		@nest &::placeholder{
			font-size: 14px;
			color: #9d9d9d;
			font-weight: 300;
		}
		@nest &--adress{
			display: none;
		}
		@nest &--area{
			padding-top: 15px;
			min-height: 120px;
			resize: none;
		}
	}
	@nest &__btn{
		float: right;
		width: 100%;
		max-width: 360px;
		display: block;
		border: 0;
		outline: none;
		padding: 0;
		font-size: 24px;
		color: #fff;
		font-weight: 700;
		min-height: 48px;
		border-radius: 5px;
		letter-spacing: .6px;
		font-family: var(--font);
		background-color: rgb(252, 122, 60);
		text-shadow: 0px 1px 0px rgba(54, 54, 54, 0.2);
		transition: background 300ms, box-shadow 300ms;
		@nest &:hover{
			background: #4ccc1d;
			box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
		}
		@media (max-width: 991px){
			float: none;
			margin: auto;
		}
		@media (max-width: 512px){
			font-size: 20px;
		}
	}
}