.bread{
	margin: 20px 0 17px;
	@nest &__link{
		font-size: 12px;
		color: #676767;
		font-weight: 300;
		text-decoration: none;
		display: inline-block;
		vertical-align: top;
		@nest &:hover{
			text-decoration: underline;
		}
	}
	@nest &__text{
		font-size: 12px;
		color: #fc7a3c;
		display: inline-block;
		vertical-align: top;
	}
}