.card-steps{
	position: absolute;
	content: '';
	right: 0;
	top: -59px;
	width: 100%;
	max-width: 360px;
	background: #fdd3be;
	padding: 50px 20px 20px;
	box-shadow: 1px 1.732px 7.2px 0.8px rgba(6, 6, 6, 0.15);
	@media (max-width: 991px){
		position: static;
		margin: 30px auto;
	}
	@nest &__title{
		display: block;
		font-size: 18px;
		color: #ff4900;
		font-family: var(--font);
		line-height: 24px;
		letter-spacing: .45px;
		margin-bottom: 30px;
	}
	@nest &__item{
		font-size: 16px;
		color: #302f2f;
		display: block;
		line-height: 23px;
		font-weight: 500;
		margin-bottom: 30px;
		position: relative;
		padding-left: 50px;
		letter-spacing: -.16px;
		@media (max-width: 512px){
			padding-left: 35px;
		}
	}
	@nest &__number{
		position: absolute;
		content: '';
		left: 10px;
		top: 1px;
		font-size: 11px;
		font-weight: 700;
		color: #fc7a3c;
		@nest &:after{
			position: absolute;
			content: '';
			width: 20px;
			height: 20px;
			left: -7px;
			top: 1px;
			border-radius: 50%;
			border: 2px solid #fc7d41;
			box-shadow: 1.5px 2.598px 3px 0px rgba(0, 0, 0, 0.2);
		}
	}
}