.order-block{
	font-size: 0;
	background: #fff;
  border-radius: 8px;
  margin-bottom: 35px;
  box-shadow: 0px 2px 7.2px 0.8px rgba(6, 6, 6, 0.15);
	@nest &__left{
		width: 70%;
		display: inline-block;
		vertical-align: middle;
		padding: 0 30px 0 70px;
		border-right: 1px solid #e9e5de;
		@media (max-width: 991px){
			padding: 0 20px;
		}
		@media (max-width: 767px){
			width: 100%;
		}
	}
	@nest &__inner{
		position: relative;
		padding: 25px 0;
		border-bottom: 1px solid #e9e5de;
		@media (max-width: 1199px){
			padding-left: 40px;
		}
	}
	@nest &__del{
		position: absolute;
		content: '';
		top: 50%;
		left: 0;
		border: 0;
		outline: none;
		padding: 0;
		transform: translateY(-50%);
		background-color: transparent;
	}
	@nest &__right{
		width: 30%;
		text-align: center;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 767px){
			width: 100%;
			margin-top: 20px;
		}
	}
	@nest &__image{
		width: 35%;
		text-align: center;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 991px){
			width: 25%;
		}
		@media (max-width: 512px){
			width: 100%;
		}
		@nest &--size{
			width: 20%;
			@media (max-width: 512px){
				width: 100%;
			}
		}
	}
	@nest &__text{
		width: 30%;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 991px){
			width: 40%;
			text-align: center;
			margin: 20px 0;
		}
		@media (max-width: 512px){
			width: 100%;
		}
	}
	@nest &__select{
		width: 35%;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 512px){
			width: 100%;
		}
		@nest &--size{
			width: 40%;
			padding-left: 45px;
			@media (max-width: 512px){
				width: 100%;
				padding-left: 0;
			}
		}
		@nest &--top{
			vertical-align: top;
		}
	}
	@nest &__title{
		font-size: 24px;
		color: #302f2f;
		font-weight: 500;
		display: block;
		line-height: 24px;
		@media (max-width: 991px){
			font-size: 20px;
			line-height: normal;
		}
	}
	@nest &__desc{
		font-size: 14px;
		color: #737373;
		font-weight: 300;
		display: block;
		line-height: 24px;
		@media (max-width: 991px){
			line-height: normal;
			margin-top: 5px;
		}
	}
	@nest &__wrap{
		text-align: left;
		display: inline-block;
	}
	@nest &__price-title{
		font-size: 18px;
		color: #302f2f;
		font-weight: 500;
		display: block;
	}
	@nest &__price{
		font-size: 24px;
		font-weight: 500;
		display: block;
		color: #5054d3;
		letter-spacing: -.6px;
	}
	@nest &__period{
		font-size: 14px;
		display: block;
		font-size: 14px;
		font-weight: 300;
		color: var(--orange);
	}
}