.leaders{
	background: #fdd3be;
	padding: 0 0 45px;
	@nest &__inner{
		padding: 0 100px;
		@media (max-width: 1199px){
			padding: 0;
		}
	}
	@nest &__btn{
		font-size: 18px;
		color: #fff;
		border: 0;
		outline: none;
		display: block;
		border-radius: 4px;
		margin: 25px auto 0;
		min-height: 48px;
		padding: 0 70px;
		font-weight: 700;
		transition: all 300ms;
		background: var(--orange);
		text-shadow: 0px 1px 0px rgba(54, 54, 54, 0.2);
		@nest &:hover{
			background: #ff5300;
		}
		@media (max-width: 512px){
			padding: 0;
			width: 100%;
			font-size: 16px;
		}
	}
}