.card-block{
	max-width: 360px;
	margin: auto;
	background: #fff;
	overflow: hidden;
	position: relative;
	border-top-left-radius: 11px;
	border-top-right-radius: 11px;
	box-shadow: 0px 2px 7.2px 0.8px rgba(6, 6, 6, 0.15);
	@nest & .leaders-block__image{
		margin-top: 25px;
		margin-bottom: 25px;
		max-width: 188px;
		display: inline-block;
	}
	@nest &__tooltip{
		position: absolute;
		content: '';
		top: 15px;
		right: 15px;
		color: #4ccc1d;
		font-size: 12px;
		font-style: italic;
		padding-right: 12px;
		@nest &:after{
			position: absolute;
			content: '';
			right: 0;
			top: 50%;
			width: 8px;
			height: 8px;
			background: #4ccc1d;
			border-radius: 50%;
			transform: translateY(-50%);
			box-shadow: 0px 2px 7.2px 0.8px rgba(6, 6, 5, 0.1);
		}
	}
	@nest &__bot{
		padding: 25px 40px;
		background: #fdd3be;
		position: relative;
		@nest &-item{
			padding: 0 5px;
			outline: none;
			@nest &:hover{
				cursor: pointer;
				@nest & img{
					border: 1px solid #fc8045;
				}
			}
		}
		@nest & img{
			margin: auto;
		}
		@nest & .slick-arrow{
			position: absolute;
			content: '';
			top: 50%;
			border: 0;
			outline: none;
			font-size: 0;
			transform: translateY(-50%);
		}
		@nest & .slick-prev{
			left: 20px;
			background: resolve('prev.png');
			width: width('prev.png');
			height: height('prev.png');
		}
		@nest & .slick-next{
			right: 20px;
			background: resolve('next.png');
			width: width('next.png');
			height: height('next.png');
		}
	}
}
.slider-for{
	text-align: center;
	@nest &__item{
		outline: none;
	}
}
.slick-current.card-block__bot-item{
	@nest & img{
		border: 1px solid #fc8045;
	}
}