.footer{
	padding: 20px 0 25px;
	background: #fff4ef;
	@media (max-width: 1199px){
		text-align: center;
	}
	@nest &__copy{
		font-size: 10px;
		color: #616161;
		font-weight: 300;
		display: block;
		text-align: center;
		margin: 30px 0 0;
		padding: 15px 0 0;
		border-top: 1px solid #fdd3be;
		@nest &-link{
			color: #0508c4;
			text-decoration: none;
			@nest &:hover{
				text-decoration: underline;
			}
		}
	}
}