.reviews{
	background: #fff4ef;
	padding: 0 0 50px;
	text-align: center;
	@nest &--bg{
		background: #fff;
	}
	@nest &__more{
		font-size: 18px;
		color: #ff4900;
		font-weight: 300;
		font-style: italic;
		margin: 30px 0 0;
		display: inline-block;
		@nest &:hover{
			text-decoration: none;
		}
	}
}