.card-info{
	@media (max-width: 991px){
		margin-top: 30px;
	}
	@nest &__title{
		font-size: 24px;
		color: #302f2f;
		display: block;
		font-weight: 500;
		margin: 10px 0 15px;
		@media (max-width: 512px){
			font-size: 20px;
		}
	}
	@nest &__desc{
		font-size: 14px;
		color: #737373;
		line-height: 24px;
		display: block;
		font-weight: 300;
		font-style: italic;
		margin: 0 0 30px;
		@nest &-orange{
			font-weight: 500;
			display: block;
			color: var(--orange);
		}
		@nest &-blue{
			display: block;
			color: var(--blue);
		}
	}
	@nest &__wrap{
		font-size: 0;
		margin-bottom: 25px;
	}
	@nest &__left{
		width: 30%;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 512px){
			width: 100%;
			margin-bottom: 5px;
		}
	}
	@nest &__right{
		width: 70%;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 1199px){
			padding-left: 10px;
		}
		@media (max-width: 512px){
			width: 100%;
			padding-left: 0;
		}
	}
	@nest &__old-price{
		font-size: 12px;
		color: #888;
		font-weight: 300;
		display: block;
		letter-spacing: -.3px;
		text-decoration: line-through;
	}
	@nest &__price{
		font-size: 18px;
		color: #5054d3;
		font-weight: 500;
		display: block;
		letter-spacing: -.45px;
	}
	@nest &__day{
		font-size: 14px;
		color: #fc7a3c;
		display: block;
		font-weight: 300;
	}
	@nest &__rent{
		outline: none;
		display: block;
		max-width: 360px;
		border-radius: 6px;
		min-height: 48px;
		font-size: 18px;
		color: #5054d3;
		width: 100%;
		font-weight: 700;
		font-family: var(--font);
		border: 2px solid #5054d3;
		background-color: transparent;
		transition: color 300ms, background 300ms, box-shadow 300ms;
		@nest &:hover{
			color: #fff;
			border: 0;
			background: #5054d3;
			box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
		}
	}
	@nest &__delivery{
		font-size: 16px;
		color: #302f2f;
		font-weight: 500;
		display: block;
	}
	@nest &__delivery-desc{
		font-size: 14px;
		color: #737373;
		font-weight: 300;
		display: block;
		font-style: italic;
	}
	@nest & .order-form__btn{
		margin-left: 0;
	}
}