.causes-block{
	width: 20%;
	text-align: center;
	display: inline-block;
	vertical-align: top;
	@media (max-width: 991px){
		width: 33%;
		margin-bottom: 15px;
	}
	@media (max-width: 767px){
		width: 50%;
	}
	@media (max-width: 512px){
		width: 100%;
	}
	@nest &__number{
		position: relative;
		font-size: 24px;
		font-weight: 700;
		color: var(--orange);
		text-align: left;
		display: block;
		margin: auto;
		padding: 20px 0 0 30px;
		background: resolve('number.jpg');
		width: width('number.jpg');
		height: height('number.jpg');
	}
	@nest &__title{
		font-size: 16px;
		color: #302f2f;
		display: block;
		font-weight: 500;
		text-align: center;
		margin: 5px 0 7px;
	}
	@nest &__text{
		font-size: 14px;
		color: #302f2f;
		display: block;
		line-height: 24px;
		font-weight: 300;
		letter-spacing: .35px;
		@media (max-width: 767px){
			padding: 0 20px;
		}
		@media (max-width: 512px){
			padding: 0;
			max-width: 47%;
			margin: auto;
		}
	}
}