.instr-bot{
	padding-bottom: 50px;
	@media (max-width: 767px){
		text-align: center;
	}
	@nest &__title{
		font-size: 24px;
		color: #ff4900;
		font-weight: 700;
		margin: 0 0 15px;
		font-family: var(--font);
		@nest &-desc{
			font-size: 16px;
			color: #302f2f;
			font-weight: 500;
			display: block;
			line-height: 24px;
		}
	}
	@nest &__video{
		display: inline-block;
		margin: 15px 0 0;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
	}
}