.modal-form{
	@nest &__label{
		font-size: 16px;
		color: #302f2f;
		display: block;
		margin: 15px 0;
		text-align: left;
		letter-spacing: .32px;
		@nest &-star{
			color: #fc7a3c;
			font-style: normal;
		}
	}
	@nest &__input{
		width: 100%;
		outline: none;
		font-size: 14px;
		color: #737373;
		font-weight: 300;
		min-height: 48px;
		background: #fff;
		padding-left: 15px;
		border-radius: 3px;
		letter-spacing: .28px;
		border: 1px solid #f0f0f0;
		@nest &::placeholder{
			font-size: 14px;
			color: #737373;
			font-weight: 300;
			letter-spacing: .28px;
		}
	}
	@nest & .header__btn{
		margin-top: 20px;
		outline: none;
	}
}