.content{
	overflow: hidden;
	@nest &--indent{
		padding-bottom: 50px;
	}
	@nest &__title{
		font-size: 24px;
		color: #ff4900;
		font-weight: 700;
		margin: 10px 0;
		font-family: var(--font);
	}
	@nest &__text{
		font-size: 16px;
		color: #302f2f;
		font-weight: 300;
		line-height: 24px;
		margin: 30px 0;
		@nest &--weight{
			font-weight: 500;
		}
		@nest &--news{
			margin: 0;
		}
		@nest &-hide{
			display: none;
			@nest &--active{
				display: block;
			}
		}
	}
	@nest &__link{
		font-size: 16px;
		font-weight: 300;
		font-style: italic;
		color: var(--blue);
		float: right;
		@nest &:hover{
			text-decoration: none;
		}
	}
	@nest &__title-image{
		@nest &--active{
			width: 170px;
		}
	}
}