#map{
	min-height: 670px;
}
#new-map{
	min-height: 670px;
}
.map{
	min-height: 670px;
	position: relative;
	@media (max-width: 767px){
		min-height: 350px;
	}
	@nest &__tooltip{
		position: absolute;
		content: '';
		left: 50%;
		top: 50px;
		z-index: 99;
		font-size: 18px;
		color: #fc7f3e;
		font-weight: 700;
		text-align: center;
		font-family: var(--font);
		padding: 13px 0 15px;
		background: #fff4ef;
		border-radius: 5px;
		transform: translateX(-50%);
		box-shadow: 2.5px 4.33px 5px 0px rgba(0, 0, 0, 0.1);
		@media (max-width: 767px){
			font-size: 16px;
			width: 100%;
			top: 25px;
		}
		@nest &-in{
			border: 0;
			outline: none;
			padding: 0;
			position: relative;
			padding: 0 70px;
			transition: color 300ms;
			background-color: transparent;
			@nest &:hover{
				color: #ff5300;
			}
			@media (max-width: 512px){
				padding: 0 0 0 50px;
			}
			@nest &:after{
				position: absolute;
				content: '';
				left: 25px;
				top: 50%;
				transform: translateY(-50%);
				background: resolve('map-tooltip.png');
				width: width('map-tooltip.png');
				height: height('map-tooltip.png');
			}
		}
	}
}