.faq-block{
	max-width: 555px;
	margin: 0 auto 20px;
	@nest &__title{
		display: block;
		color: #fff;
		font-weight: 700;
		background: #8084fa;
		padding: 15px 0 15px 20px;
	}
	@nest &__text{
		display: block;
		font-size: 14px;
		color: #302f2f;
		font-weight: 300;
		line-height: 24px;
		letter-spacing: .35px;
		background: #ecedff;
		padding: 15px 20px 40px;
	}
}