.datepicker-here{
	width: 100%;
	font-size: 14px;
	text-align: left;
	padding-left: 10px;
	min-height: 25px;
	display: inline-block;
	margin: auto;
	color: #302f2f;
	cursor: pointer;
	font-weight: 500;
	position: relative;
	background: #fff4ef;
	border-radius: 5px;
	border: 1px solid #e9e5de;
	box-shadow: 1px 1.732px 1.96px 0.04px rgba(6, 6, 6, 0.15);
	outline: none;
}

.datepicker{
	border: 1px solid #e9e5de !important;
	box-shadow: 1px 1.732px 1.96px 0.04px rgba(6, 6, 6, 0.15) !important;
	border-radius: 5px !important;
	background: #fff4ef;
	&--nav{
		background: #ffe6da !important;
	}
	&--pointer{
		background: #ffe6da !important;
	}
}
