.small-select{
	width: 100px;
	text-align: center;
	margin-right: 30px;
	display: inline-block;
	@nest &:last-child{
		margin-right: 0;
		@media (max-width: 512px){
			margin: 0 auto 15px;
		}
	}
	@media (max-width: 1199px){
		margin-right: 0;
		display: block;
		margin-bottom: 15px;
	}
	@media (max-width: 512px){
		margin: 0 auto 15px;
	}
	@nest &__desc{
		font-size: 14px;
		color: #737373;
		color: #000;
		font-weight: 300;
		display: block;
		margin-bottom: 5px;
		letter-spacing: -.35px;
	}
}
.datepicker-here{
	@nest &::placeholder{
		font-size: 14px;
		color: #000;
		font-weight: 300;
	}
}