.tabs{
	margin: 40px 0 50px;
	padding-right: 390px;
	position: relative;
	@media (max-width: 991px){
		padding-right: 0;
	}
	@nest &:after{
		position: absolute;
		content: '';
		top: 25px;
		right: 0;
		z-index: -1;
		background: resolve('tabs.jpg');
		width: width('tabs.jpg');
		height: height('tabs.jpg');
		@media (max-width: 991px){
			display: none;
		}
	}
	@nest &__title{
		font-size: 16px;
		color: #302f2f;
		font-weight: 500;
		margin: 0;
		line-height: 24px;
	}
	@nest &__text{
		font-size: 14px;
		color: #302f2f;
		line-height: 24px;
		margin: 30px 0;
		font-weight: 300;
		letter-spacing: -.35px;
	}
	@nest &__list{
		padding: 0;
		margin: 0;
		list-style: none;
	}
	@nest &__item{
		font-size: 14px;
		color: #302f2f;
		font-weight: 300;
		margin: 10px 0 0;
	}
}