.modal{
	display: none;
	text-align: center;
	max-width: 750px;
	background: #fbf7f5;
	padding: 35px 165px 40px;
	@media (max-width: 767px){
		padding: 30px;
	}
	@nest &__title{
		font-size: 22px;
		color: #fc7a3c;
		font-weight: 400;
		line-height: 24px;
		letter-spacing: .44px;
		margin: 0 0 25px;
		@media (max-width: 512px){
			font-size: 18px;
		}
	}
	@nest &--thn{
		max-width: 575px;
		background: #fff;
		position: relative;
		padding: 35px 120px 50px;
		@media (max-width: 767px){
			padding: 30px;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: -140px;
			bottom: -20px;
			-webkit-animation: show 500ms ease;
			background: resolve('modal-after.png');
			width: width('modal-after.png');
			height: height('modal-after.png');
			@media (max-width: 767px){
				display: none;
			}
		}
		@nest & .modal__title{
			margin: 0 auto 20px;
			letter-spacing: 0;
		}
	}
	@nest &__text{
		font-size: 14px;
		color: #000;
		display: block;
		margin: 0 0 10px;
		line-height: normal;
		@nest &-bold{
			font-weight: 700;
		}
		@nest &-italic{
			font-style: italic;
		}
	}
	@nest &__res{
		font-size: 24px;
		color: #000;
		font-weight: 700;
		display: block;
		line-height: normal;
	}
}
.fancybox-inner{
	overflow: visible !important;
}
.fancybox-skin{
	padding: 0 !important;
	box-shadow: none !important;
}
.fancybox-close{
	right: 25px;
	top: 25px;
	background: resolve('close1.png');
	width: width('close1.png');
	height: height('close1.png');
	@media (max-width: 767px){
		top: 5px;
		right: 5px;
	}
}
.fancybox-new{
	@nest & .fancybox-skin{
		background: #fff !important;
		padding: 40px 135px !important;
	}
	@nest & .fancybox-nav{
		position: absolute;
		content: '';
		top: 50%;
		transform: translateY(-50%);
	}
	@nest & .fancybox-prev{
		left: -100px;
		background: resolve('card-left.png');
		width: width('card-left.png');
		height: height('card-left.png');
		@nest & span{
			display: none;
		}
	}
	@nest & .fancybox-next{
		right: -100px;
		background: resolve('card-right.png');
		width: width('card-right.png');
		height: height('card-right.png');
		@nest & span{
			display: none;
		}
	}
}

@-webkit-keyframes show{
	0%{left: -150%;}
	50%{left: -50%;}
	100%{left: -140px;}
}