.select{
	width: 130px;
	margin: auto;
	min-height: 48px;
	font-size: 18px;
	color: #302f2f;
	cursor: pointer;
	font-weight: 500;
	text-align: center;
	position: relative;
	background: #fff4ef;
	border-radius: 5px;
	border: 1px solid #e9e5de;
	box-shadow: 1px 1.732px 1.96px 0.04px rgba(6, 6, 6, 0.15);
	@nest &--left{
		margin-left: 0;
		@media (max-width: 512px){
			margin: auto;
		}
	}
	@nest &--sm{
		width: 100%;
		font-size: 14px;
		text-align: left;
		padding-left: 10px;
		min-height: 25px;
		display: inline-block;
		@nest & .select__current{
			line-height: 25px;
			@nest &:after{
				right: 7px;
			}
		}
		@nest & .select__item{
			line-height: 25px;
			padding-left: 10px;
		}
	}
	@nest &__current{
		line-height: 48px;
		position: relative;
		display: block;
		@nest &:after{
			position: absolute;
			content: '';
			right: 13px;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('arrow.png');
			width: width('arrow.png');
			height: height('arrow.png');
		}
	}
	@nest &__list{
		position: absolute;
		content: '';
		top: 100%;
		left: 0;
		right: 0;
		z-index: 10;
		padding: 0;
		margin: 0;
		display: none;
		list-style: none;
		background: #fff4ef;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
		border: 1px solid #e9e5de;
		box-shadow: 1px 1.732px 1.96px 0.04px rgba(6, 6, 6, 0.15);
	}
	@nest &__item{
		line-height: 48px;
		cursor: pointer;
		transition: background 300ms;
		@nest &:hover{
			background: #ffe6da;
		}
	}
}