.faq-form{
	position: relative;
	@media (max-width: 991px){
		padding-bottom: 50px;
	}
	@nest &:before{
		position: absolute;
		content: '';
		left: -100px;
		bottom: 0;
		background: resolve('faq1.jpg');
		width: width('faq1.jpg');
		height: height('faq1.jpg');
		@media (max-width: 1199px){
			left: -210px;
		}
		@media (max-width: 991px){
			display: none;
		}
	}
	@nest &:after{
		position: absolute;
		content: '';
		right: 0;
		bottom: 70px;
		background: resolve('faq2.png');
		width: width('faq2.png');
		height: height('faq2.png');
		@media (max-width: 1199px){
			display: none;
		}
	}
	@nest &__inner{
		max-width: 555px;
		margin: auto;
		position: relative;
		@media (max-width: 991px){
			font-size: 0;
		}
	}
	@nest &__input{
		width: 100%;
		border: 0;
		font-size: 16px;
		font-weight: 300;
		color: #302f2f;
		outline: none;
		min-height: 48px;
		padding-left: 17px;
		margin-bottom: 24px;
		box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
		@nest &::placeholder{
			font-size: 16px;
			font-weight: 300;
			color: #9d9d9d;
		}
		@nest &--area{
			min-height: 192px;
			resize: none;
			padding-top: 15px;
		}
	}
	@nest &__btn{
		font-size: 18px;
		color: #fff;
		border: 0;
		outline: none;
		min-height: 48px;
		display: block;
		width: 100%;
		font-weight: 700;
		font-family: var(--font);
		border-radius: 5px;
		background: #fc7a3c;
		transition: all 300ms;
		@nest &:hover{
			background: #4ccc1d;
			box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
		}
		@media (max-width: 991px){
			position: absolute;
			content: '';
			bottom: -265px;
			left: 0;
			right: 0;
		}
	}
}