.reviews-block{
	max-width: 260px;
	margin: auto;
	margin: 0 0 25px;
	background-color: #f9e4d9;
	background-repeat: no-repeat;
	@media (max-width: 1199px){
		margin-bottom: 20px;
	}
	@nest &--left{
		@media (max-width: 991px){
			margin-right: 0;
			margin-left: auto;
		}
	}
	@nest &--right{
		@media (max-width: 991px){
			margin-left: 0;
			margin-right: auto;
		}
	}
	@media (max-width: 767px){
		margin-left: auto;
		margin-right: auto;
	}
	@nest &__text{
		font-size: 14px;
		color: #313131;
		font-weight: 300;
		line-height: 24px;
		display: block;
		text-align: left;
		font-family: var(--font);
		margin-bottom: 10px;
		padding: 280px 15px 0;
	}
	@nest &__name{
		font-size: 14px;
		color: #313131;
		font-weight: 700;
		display: block;
		text-align: right;
		padding: 0 15px 15px 0;
		font-family: var(--font);
	}
}