.catalogue-block{
	clear: fix;
	max-width: 360px;
	margin: auto;
	background: #fff;
	overflow: hidden;
	position: relative;
	padding: 25px 30px 25px;
	border: 1px solid #e9e5de;
	border-radius: 11px;
	margin-bottom: 32px;
	transition: box-shadow 300ms;
	box-shadow: 0px 1px 0px 0px rgba(2, 2, 2, 0.06);
	@nest &:hover{
		box-shadow: 0px 2px 7.2px 0.8px rgba(6, 6, 6, 0.15);
	}
	@nest &--left{
		@media (max-width: 1199px){
			margin-left: auto;
			margin-right: 0;
		}
		@media (max-width: 991px){
			margin: 0 auto 32px;
		}
	}
	@nest &--right{
		@media (max-width: 1199px){
			margin-right: auto;
			margin-left: 0;
		}
		@media (max-width: 991px){
			margin: 0 auto 32px;
		}
	}
	@media (max-width: 512px){
		text-align: center;
	}
	@nest &__image{
		float: left;
		margin-left: 25px;
		@media (max-width: 512px){
			float: none;
			display: block;
			margin: auto;
		}
	}
	@nest &__content{
		float: left;
		max-width: 115px;
		padding-left: 10px;
		@media (max-width: 512px){
			float: none;
			padding-left: 0;
			max-width: 100%;
		}
	}
	@nest &__title{
		font-size: 18px;
		color: #302f2f;
		font-weight: 500;
		line-height: 24px;
		display: block;
		text-decoration: none;
		margin: 10px 0 18px;
		@media (max-width: 512px){
			margin: 20px 0 5px;
		}
	}
	@nest &__price-text{
		font-size: 14px;
		color: var(--orange);
		font-weight: 500;
		font-style: italic;
		display: block;
		letter-spacing: .28px;
		margin: 0 0 20px;
		@media (max-width: 512px){
			margin-bottom: 10px;
		}
	}
	@nest &__desc{
		font-size: 14px;
		display: block;
		font-weight: 500;
		font-style: italic;
		color: var(--blue);
		margin: 0 0 28px;
		@nest &-italic{
			font-weight: 300;
			color: #737373;
		}
	}
	@nest &__bot{
		clear: both;
		padding: 15px 0 0;
		border-top: 1px solid #e9e5de;
	}
	@nest &__item{
		float: left;
		width: 100%;
		max-width: 148px;
		@media (max-width: 512px){
			float: none;
			display: block;
			margin: auto;
		}
	}
	@nest &__old-price{
		font-size: 12px;
		color: #888;
		display: 300px;
		display: block;
		padding-left: 10px;
		letter-spacing: -.3px;
		text-decoration: line-through;
		@media (max-width: 512px){
			padding-left: 0;
		}
	}
	@nest &__price{
		font-size: 22px;
		font-weight: 500;
		display: block;
		color: var(--blue);
		letter-spacing: -.55px;
		padding-left: 10px;
		margin-bottom: 15px;
		@media (max-width: 512px){
			margin-bottom: 10px;
		}
		@media (max-width: 512px){
			padding-left: 0;
		}
	}
	@nest &__day{
		font-size: 12px;
		display: block;
		font-weight: 300;
		color: var(--orange);
		letter-spacing: -.3px;
		padding-left: 10px;
		@media (max-width: 512px){
			margin-top: 15px;
			padding-left: 0;
		}
	}
	@nest &__rent{
		border: 0;
		outline: none;
		min-height: 48px;
		width: 120px;
		font-size: 14px;
		border-radius: 4px;
		color: var(--blue);
		border: 2px solid #5054d3;
		background-color: transparent;
		transition: color 300ms, background 300ms, box-shadow 300ms;
		@nest &:hover{
			color: #fff;
			border: 0;
			background: var(--blue);
			box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
		}
	}
}