.leaders-block{
	max-width: 225px;
	margin: auto;
	padding: 20px;
	position: relative;
	background: #fff;
	border-radius: 8px;
	overflow: hidden;
	box-shadow: 0px 1px 0px 0px rgba(2, 2, 2, 0.06);
	@media (max-width: 991px){
		margin-bottom: 20px;
	}
	@nest &--left{
		@media (max-width: 991px){
			margin-left: auto;
			margin-right: 0;
		}
	}
	@nest &--right{
		@media (max-width: 991px){
			margin-left: 0;
			margin-right: auto;
		}
	}
	@media (max-width: 512px){
		margin: auto;
		margin-bottom: 20px;
	}
	@nest &__tooltip{
		position: absolute;
		content: '';
		left: -7px;
		top: -7px;
		width: 60px;
		height: 60px;
		font-size: 18px;
		color: #fff;
		padding: 13px 0 0;
		display: block;
		line-height: normal;
		text-align: center;
		background: #4ccc1d;
		border-radius: 50%;
		box-shadow: 2.5px 4.33px 5px 0px rgba(0, 0, 0, 0.3);
		@nest &--sm{
			font-size: 10px;
			display: block;
		}
		@nest &--bg{
			font-size: 16px;
			padding-top: 15px;
			background: #3f44fa;
		}
		@nest &--purple{
			font-size: 24px;
			color: var(--orange);
			font-weight: 700;
			padding-top: 17px;
			background: #fdd3be;
		}
	}
	@nest &__image{
		display: block;
		margin: 0 auto 10px;
	}
	@nest &__name{
		font-size: 16px;
		color: #302f2f;
		font-weight: 500;
		margin-bottom: 10px;
		text-decoration: none;
		display: block;
		transition: all 300ms;
		@nest &:hover{
			color: #5054d3;
			text-decoration: underline;
		}
	}
	@nest &__desc{
		font-size: 14px;
		color: #737373;
		font-weight: 300;
		font-style: italic;
		display: block;
		max-width: 95px;
		line-height: 16px;
	}
	@nest &__price{
		font-size: 22px;
		color: var(--blue);
		font-weight: 500;
		display: block;
		text-align: right;
		margin-top: -17px;
		margin-bottom: 25px;
	}
	@nest &__btn{
		font-size: 24px;
		color: #fff;
		width: 120px;
		display: block;
		margin: auto;
		border: 0;
		outline: none;
		min-height: 48px;
		font-weight: 700;
		border-radius: 4px;
		transition: all 300ms;
		background: var(--orange);
		font-family: var(--font);
		text-shadow: 0px 1px 0px rgba(54, 54, 54, 0.2);
		@nest &:hover{
			background: #4ccc1d;
			box-shadow: inset -3.536px 3.536px 5px 0px rgba(0, 0, 0, 0.3);
		}
		@nest &--catalogue{
			margin-left: 0;
			@media (max-width: 512px){
				margin-left: auto;
			}
		}
	}
}