.contacts{
	@nest &__text{
		font-size: 16px;
		color: #302f2f;
		margin: 0;
		font-weight: 300;
		line-height: 24px;
		letter-spacing: .32px;
		@nest &-bold{
			font-weight: 500;
		}
	}
	@nest &__title{
		font-size: 14px;
		color: #302f2f;
		display: block;
		font-weight: 500;
		line-height: 24px;
		letter-spacing: .28px;
		margin: 0 0 8px;
		@media (max-width: 1199px){
			margin: 20px 0 10px;
		}
	}
	@nest &__desc{
		font-size: 14px;
		color: #302f2f;
		font-weight: 300;
		line-height: 24px;
		display: block;
		max-width: 165px;
		@nest &-size{
			letter-spacing: -.35px;
		}
		@nest &--color{
			color: #676767;
		}
	}
}