.footer-list{
	padding: 0;
	margin: 0;
	list-style: none;
	display: inline-block;
	vertical-align: top;
	@nest &--indent{
		margin: 0 20px 0 60px;
		@media (max-width: 767px){
			margin-left: 0;
		}
	}
	@nest &__item{
		display: block;
		margin-bottom: 7px;
	}
	@nest &__link{
		font-size: 14px;
		color: #302f2f;
		text-decoration: none;
		transition: all 300ms;
		@nest &:hover{
			color: var(--orange);
			text-decoration: underline;
		}
	}
}