.inner-content{
	@nest &__title{
		font-size: 24px;
		font-weight: 700;
		color: var(--orange);
		font-family: var(--font);
		letter-spacing: .6px;
		margin: 35px 0;
		@media (max-width: 991px){
			text-align: center;
		}
		@media (max-width: 512px){
			font-size: 20px;
		}
	}
	@nest &__text{
		font-size: 16px;
		color: #302f2f;
		line-height: 24px;
		margin: 25px 0;
		letter-spacing: .32px;
		max-width: 610px;
	}
}