.causes{
	font-size: 0;
	@media (max-width: 1318px){
		overflow: hidden;
	}
	@nest &__wrap{
		padding: 0 100px 40px;
		position: relative;
		@media (max-width: 991px){
			padding: 0 0 20px;
			text-align: center;
		}
		@nest &:after{
			position: absolute;
			content: '';
			right: -65px;
			bottom: 25px;
			z-index: -1;
			background: resolve('causes.jpg');
			width: width('causes.jpg');
			height: height('causes.jpg');
			@media (max-width: 1199px){
				right: -110px;
			}
			@media (max-width: 991px){
				display: none;
			}
		}
	}
}