.video{
	padding: 0 0 50px;
	background: #fdd3be;
	@nest &__text{
		font-size: 14px;
		color: #302f2f;
		font-weight: 300;
		line-height: 24px;
		letter-spacing: .28px;
		margin: 0 0 30px;
	}
	@nest &__image{
		float: left;
		margin-right: 13px;
	}
	@nest &__link{
		font-size: 14px;
		color: var(--blue);
		font-weight: 300;
		font-style: italic;
		display: inline-block;
		@nest &:hover{
			text-decoration: none;
		}
	}
	@nest &-wrap{
		position: relative;
		max-width: 455px;
		margin: auto;
		min-height: 335px;
		@nest &__item{
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			@media (max-width: 991px){
				top: 20px;
			}
		}
	}
}