.how-block{
	position: relative;
  border-radius: 5px;
  background: #fff;
  overflow: hidden;
  height: 120px;
  box-shadow: 2.5px 4.33px 5px 0px rgba(0, 0, 0, 0.1);
  @media (max-width: 991px){
  	margin-bottom: 32px;
  }
  @nest &__text{
  	position: absolute;
  	content: '';
  	top: 50%;
  	left: 0;
  	width: 100%;
  	font-size: 14px;
  	padding-left: 65px;
  	padding-right: 18px;
  	line-height: 24px;
  	font-weight: 300;
  	color: var(--blue);
  	transform: translateY(-50%);
    @media (max-width: 1199px){
      font-size: 12px;
      line-height: 20px;
    }
    @media (max-width: 991px){
      padding-right: 32px;
    }
    @media (max-width: 512px){
      padding-right: 45px;
    }
  }
}